.product {
  &__specifications {
    height: calc(100% - 2rem);
    display: flex;
    justify-content: center;

    .tabs__labels {
      padding-top: 5rem;

      @media screen and (max-width: 479.98px) {
        padding-top: 1rem;
      }
    }

    .tabs__content {
      height: calc(100% - 11rem);
      max-width: 84.5rem;
    }
  }

  &__preferences {
    display: flex;
    justify-content: space-around;

    &-item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      div {
        margin-top: 20px;
      }
    }
  }

  &__description {
    color: #404040;
    font-size: 1.4rem;
    font-weight: 300;
    line-height: 1.6;
    letter-spacing: 1.4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

  }
}
