.product {
  &__info {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 60vh;

    @media screen and (max-width: 479.98px) {
      height: 70vh;
    }
  }

  .link {

    .fa {
      margin-right: 10px;
      font-size: 2rem;
      vertical-align: middle;
    }
  }

  &__title {
    color: #404040;
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 3px;
  }

  &__price {
    color: #ff4f37;
    letter-spacing: 1.6px;
  }

  &__caption {
    color: #404040;
    font-size: 1.4rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.4px;
  }

  .size {
    justify-content: center;
  }

  .colors {
    margin: 0 -.75rem -1.5rem;
  }


  &__share {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

    .fa {
      margin-right: 1rem;
      font-size: 2rem;
      color: #ff4f37;
    }

  }
}
